import * as React from 'react';
import Backdrop from '@mui/material/Backdrop';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Close from '@mui/icons-material/Close';
import IconButton from '@mui/material/IconButton';
import Divider from '@mui/material/Divider';


const style = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'rgba(221, 229, 217, 1)',
  border: '2px solid rgba(251, 90, 80, 1)',
  boxShadow: 24,
  p: 4,
};

const stybut = {
  // position: 'absolute' as 'absolute',
  // top: '10%',
  // left: '10%',
  zIndex: '9999',
};

export default function Legendblock() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
      <Button sx={stybut} onClick={handleOpen}>Taxation des ressources naturelles - Lgende</Button>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={open}
        onClose={handleClose}
        closeAfterTransition
        disableEnforceFocus
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <Box sx={style}>
            <IconButton onClick={handleClose} className="closebutton">
              <Close />
            </IconButton>
            <Typography>
              <b>Taxation du circuit des makala et des ressources naturelles le long de l'axe routier Ishasha-Rutshuru</b><br /><br /><br />
              <Divider />
            </Typography>
            <Typography>
              <div className='blockalert'></div>Flux du commerce illgal des ressources naturelles, de produits forestiers et des makala (charbon de bois)<br /><br /><br />
            </Typography>
            <Typography>
            <b>Barrires routires physiques et virtuelles</b><br /><br />
            <div className='blockW'></div>Barrires routires documentes depuis 2023 et contrles par les Wazalendos. Se consacrant principalement  la taxation des pages et au recrutement de locaux pour le salongo, les Wazalendos taxent galement l'accs aux ressources naturelles et le transport des ressources exploites<br /><br />
            <div className='blockN'></div>Barrires routires documentes depuis 2017, qui assurent la taxation de toutes les ressources naturelles, dont les Wazalendos rglementent l'accs aux ressources et l'vacuation des ressources exploites<br /><br />
            <div className='blockF'></div>Barrires routires documentes depuis 2017 et contrles par des groupes arms (FARDC, FDLR, Nyatura FPPH). Ces barrires sont destines  taxer le makala et desles planches de bois, soumettant aux impts les bcherons, les charbonniers et les vendeurs de charbon<br /><br />
            </Typography>
          </Box>
        </Fade>
      </Modal>
    </div>
  );
}
