              <Typography component='div' className="sentence" sx={{ mt: 2 }}>Ce sont nous, les femmes, qui souffrons le plus du manque d'accs aux ressources forestires, de la dgradation des cosystmes, des effets du systme oppressif des conflits. Nous sommes voles, violes et tues alors que nous cherchons de l'eau et des vivres, du bois pour s'abriter, cuisiner et se chauffer. Nous sommes souvent obligs de voyager pendant des heures la nuit pour trouver une fontaine d'eau en esprant ne pas croiser le chemin de violeurs et d'assassins, tandis que nous assistons  la destruction de la fort autour de nous</Typography>
